.results {
  display: block;
  list-style-type: none;
  margin-bottom: 50px;
}

.results > li {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  margin: 15px 0;
}

.results > li .infowrapper {
  margin-bottom: 15px;
  width: 60vw;
}

.results > li .info {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.results .info > ul,
.results .info > div {
  margin: 0 2vw;
  width: 25%;
}

.results .info > ul {
  width: 40%;
}

.results .noimage,
.results img {
  width: 12vw;
  max-width: 100%;
  height: 100%;
}

.results .noimage {
  height: 25vh;
  background-color: #333;
  padding: 20px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.results .notfound {
  font-style: italic;
  color: #999;
  height: 75px;
  -ms-flex-align: center;
      align-items: center;
  height: 20vh;
}

.results .info .customRating {
  -webkit-transform-origin: 50% 50%;
      -ms-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
  -webkit-transform: rotate(180deg);
      -ms-transform: rotate(180deg);
          transform: rotate(180deg);
  width: 5vw;
  margin: 0 15px;
}

.results .title {
  display: block;
  margin-bottom: 2vmin;
}

.results .links {
  font-size: 0.9em;
}

.results .links a {
  display: block;
  margin-bottom: 1vh;
}

.ratings .comment .value {
  font-style: italic;
  font-weight: 200;
  font-size: 3vmin;
  margin-left: 0;
  padding-bottom: 8px;
  display: inline-block;
}

@media screen and (min-width: 640px) {
  .ratings .comment .value {
    font-size: 2.5vmin;
  }
}

.ratings {
  text-align: left;
  list-style-type: none;
  font-size: 0.9em;
}

.ratings li {
  margin-bottom: 1vh;
}

.ratings .vendor {
  opacity: 0.7;
}
.ratings .value {
  margin-left: 1vw;
  color: #fff;
}



