/* Animation */
@-webkit-keyframes blurInAndOut {
    0% {-webkit-filter: blur(6px);filter: blur(6px); opacity: 0;}
    55% {-webkit-filter: blur(1px);filter: blur(1px); opacity: 0.2;}
    75% {-webkit-filter: blur(2px);filter: blur(2px); opacity: 0.9;}
    100% {-webkit-filter: blur(6px);filter: blur(6px); opacity: 0;}
}
@keyframes blurInAndOut {
    0% {-webkit-filter: blur(6px);filter: blur(6px); opacity: 0;}
    55% {-webkit-filter: blur(1px);filter: blur(1px); opacity: 0.2;}
    75% {-webkit-filter: blur(2px);filter: blur(2px); opacity: 0.9;}
    100% {-webkit-filter: blur(6px);filter: blur(6px); opacity: 0;}
}


.spinner {
    opacity: 1;
    position: absolute;
    top: 55vmin;
    left: calc(50% - 15px);
    border-radius: 50%;
    height: 20px;
    width: 20px;
}

.spinner.active {
    opacity: 1;
    -webkit-animation-name: blurInAndOut;
            animation-name: blurInAndOut;
    -webkit-animation-duration: 3s;
            animation-duration: 3s;
    -webkit-animation-iteration-count:infinite;
            animation-iteration-count:infinite;
    border: 2px solid #222;
    background: -webkit-radial-gradient(center ellipse, #a90329 0%,#8f0222 44%,#6d0019 100%);
    background: -o-radial-gradient(center ellipse, #a90329 0%,#8f0222 44%,#6d0019 100%);
    background: radial-gradient(ellipse at center, #a90329 0%,#8f0222 44%,#6d0019 100%); 
}
  
.spinner.active::after {
    position: absolute;
    content: ' ';
    top: 40%;
    left: 40%;
    border-radius: 50%;
    height: 20%;
    width: 20%;
    -webkit-filter: blur(1px);
            filter: blur(1px);
    background-color: #FFD200;
}

.spinner.active::before {
    position: absolute;
    content: ' ';
    top: 13%;
    left: 27%;
    border-radius: 30%;
    height: 30%;
    width: 15%;
    opacity: 0.4;
    -webkit-filter: blur(1px);
            filter: blur(1px);
    background-color: #FFFFFF;
    -webkit-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
            transform: rotate(45deg);
}




