/* Animation */
@-webkit-keyframes fadeInOut {
    0% {opacity: 0}
    25% {opacity: 1}
    75% {opacity: 1}
    100% {opacity: 0}
}
@keyframes fadeInOut {
    0% {opacity: 0}
    25% {opacity: 1}
    75% {opacity: 1}
    100% {opacity: 0}
}

.buttonTitle.result {
    opacity: 0;
    display: block;
    color: #fff;
    font-size: 14px;
    -webkit-animation-name: fadeInOut;
            animation-name: fadeInOut;
    -webkit-animation-duration: 3s;
            animation-duration: 3s;
}


